.container {
    font-family: "Poppins", sans-serif;
    padding: 3rem 1rem;
    background: #f0f8ff; /* soft blue background for quizzes */
    min-height: 80vh;
    text-align: center;
}

.title {
    font-size: 2.5rem;
    color: #4a90e2; /* engaging blue for quizzes */
    margin-bottom: 0.5rem;
    font-weight: 700;
}

.subtitle {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 2.5rem;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.5;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 2rem;
    max-width: 1000px;
    margin: 0 auto;
}

.card {
    background: #fff;
    border-radius: 20px;
    padding: 2rem 1.5rem;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
    cursor: pointer;
    transition:
        transform 0.3s,
        box-shadow 0.3s;
    position: relative;
}

.card:hover {
    transform: translateY(-6px);
    box-shadow: 0 16px 36px rgba(0, 0, 0, 0.12);
}

.card h2 {
    font-size: 1.6rem;
    color: #4a90e2;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.card p {
    font-size: 1rem;
    color: #555;
    line-height: 1.5;
}

.card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background: linear-gradient(135deg, #d0e7ff, #74b9ff);
    opacity: 0;
    transition: opacity 0.3s;
    z-index: 0;
}

.card:hover::before {
    opacity: 0.2;
}

.card h2,
.card p {
    position: relative;
    z-index: 1;
}

@media screen and (max-width: 768px) {
    .title {
        font-size: 2rem;
    }

    .subtitle {
        font-size: 1rem;
    }

    .card h2 {
        font-size: 1.4rem;
    }

    .card p {
        font-size: 0.95rem;
    }
}
