.container {
    font-family: "Poppins", sans-serif;
    padding: 3rem 1rem;
    background: #fefefe;
    min-height: 80vh;
    text-align: center;
}

.title {
    font-size: 2.8rem;
    color: #4a90e2;
    margin-bottom: 0.5rem;
    font-weight: 700;
}

.subtitle {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 2.5rem;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.5;
}

/* Grades grid and cards */
.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 2rem;
    max-width: 1000px;
    margin: 0 auto 3rem auto;
}

.card {
    background: #ffffff;
    border-radius: 20px;
    padding: 2rem 1.5rem;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
    cursor: pointer;
    transition:
        transform 0.3s,
        box-shadow 0.3s,
        background 0.3s;
    position: relative;
}

.card:hover {
    transform: translateY(-6px);
    box-shadow: 0 16px 36px rgba(0, 0, 0, 0.12);
    background: #e0f7fa;
}

.card h2 {
    font-size: 1.6rem;
    color: #00796b;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.card p {
    font-size: 1rem;
    color: #555;
    line-height: 1.5;
}

.card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background: linear-gradient(135deg, #d0f0fd, #b2ebf2);
    opacity: 0;
    transition: opacity 0.3s;
    z-index: 0;
}

.card:hover::before {
    opacity: 0.2;
}

.card h2,
.card p {
    position: relative;
    z-index: 1;
}

/* Extra content sections styled like cards */
.content,
.extraContent {
    max-width: 900px;
    margin: 2rem auto;
    padding: 2rem 1.5rem;
    text-align: left;
    background: #ffffff;
    border-radius: 20px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
    position: relative;
    transition:
        transform 0.3s,
        box-shadow 0.3s,
        background 0.3s;
}

.content:hover,
.extraContent:hover {
    transform: translateY(-4px);
    box-shadow: 0 14px 32px rgba(0, 0, 0, 0.1);
    background: #f0f9fa;
}

.content p,
.extraContent p {
    font-size: 1rem;
    color: #555;
    line-height: 1.6;
    margin-bottom: 1rem;
}

.extraContent h2 {
    font-size: 1.8rem;
    color: #00796b;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-weight: 700;
}

.extraContent h3 {
    font-size: 1.2rem;
    color: #4a90e2;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.extraContent ul {
    margin: 1rem 0 1.5rem 1.5rem;
    padding-left: 1rem;
    list-style: disc;
    color: #333;
}

.extraContent li {
    margin-bottom: 0.6rem;
    font-size: 1rem;
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
    .title {
        font-size: 2rem;
    }

    .subtitle {
        font-size: 1rem;
    }

    .card h2 {
        font-size: 1.4rem;
    }

    .card p,
    .content p,
    .extraContent p {
        font-size: 0.95rem;
    }

    .extraContent h2 {
        font-size: 1.4rem;
    }

    .extraContent h3 {
        font-size: 1.1rem;
    }
}
