.wrapper {
    font-family: "Poppins", sans-serif;
    padding: 3rem 1rem;
    background: #fdfdfd;
    min-height: 80vh;
    text-align: center;
}

.title {
    font-size: 2.5rem;
    color: #4a90e2;
    font-weight: 700;
    margin-bottom: 2rem;
}

.subjectGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 1.8rem;
    max-width: 1000px;
    margin: 0 auto;
}

.subjectCard {
    background: #ffffff;
    border-radius: 20px;
    padding: 2rem 1rem;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
    cursor: pointer;
    transition:
        transform 0.3s,
        box-shadow 0.3s,
        background 0.3s;
    font-weight: 600;
    color: #00796b;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.subjectCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 14px 30px rgba(0, 0, 0, 0.12);
    background: #e0f7fa;
}

.noSubjects {
    font-size: 1.2rem;
    color: #888;
    margin-top: 2rem;
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
    .title {
        font-size: 2rem;
    }

    .subjectCard {
        padding: 1.5rem 0.8rem;
        font-size: 0.95rem;
    }
}
