* { 
  margin: 0; 
  padding: 0; 
  box-sizing: border-box; 
}

body { 
  font-family: 'Inter', sans-serif; 
  background: #f5f5f5;
  padding: 20px 0px; 
  color: #222; 
  min-height: 100vh; 
  overflow-x: hidden; 
}

/* Remove underline from links */
a {
  text-decoration: none;
  color: inherit;
}