/* Global Header */
.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
    background-color: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(12px);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    z-index: 1000;
    font-family: "Poppins", sans-serif;
}

/* Logo */
.logo {
    font-size: 28px;
    font-weight: 800;
    display: flex;
    gap: 5px;
}
.awrora {
    background: linear-gradient(
        270deg,
        #ff3c00,
        #ff9900,
        #33cc33,
        #0066ff,
        #cc00ff
    );
    background-size: 1000% 100%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: gradientMove 6s linear infinite;
}
.iq {
    color: #00796b;
    font-weight: 900;
}
@keyframes gradientMove {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

/* Navigation */
.navLinks {
    display: flex;
    gap: 25px;
    align-items: center;
}
.link {
    text-decoration: none;
    color: #333;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 6px 10px;
    border-radius: 8px;
    transition: all 0.3s ease;
}
.link:hover {
    color: #00796b;
    transform: scale(1.05);
    text-shadow: 0 0 4px #00796b;
    background-color: rgba(0, 121, 107, 0.08);
}
.activeLink {
    color: #00796b;
    font-weight: 600;
}

/* Dropdown */
.dropdown {
    position: relative;
}
.dropbtn {
    background: none;
    border: none;
    font-weight: 500;
    color: #333;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
    padding: 6px 10px;
    border-radius: 8px;
    transition: all 0.3s ease;
}
.dropbtn:hover {
    color: #00796b;
    background-color: rgba(0, 121, 107, 0.08);
}
.dropdownContent {
    display: none;
    position: absolute;
    top: 38px;
    right: 0;
    background: white;
    min-width: 160px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
    z-index: 1001;
    flex-direction: column;
}
.dropdownContent a {
    padding: 10px 15px;
    display: flex;
    align-items: center;
    gap: 6px;
    color: #333;
    font-weight: 500;
    text-decoration: none;
    transition: all 0.3s ease;
}
.dropdownContent a:hover {
    background-color: rgba(0, 121, 107, 0.08);
    color: #00796b;
}
.show {
    display: flex;
    flex-direction: column;
}

/* Hamburger */
.hamburger {
    display: none;
    flex-direction: column;
    gap: 6px;
    cursor: pointer;
    background: none;
    border: none;
    outline: none;
}

/* Overlay */
.overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.25);
    z-index: 900;
}
.overlay.active {
    display: block;
}

/* Mobile Styles */
@media (max-width: 768px) {
    .hamburger {
        display: flex;
    }
    .navLinks {
        position: fixed;
        top: 0;
        right: -100%;
        width: 75%;
        height: 100vh;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 30px;
        background: rgba(255, 255, 255, 0.95);
        backdrop-filter: blur(12px);
        transition: right 0.3s ease;
    }
    .navLinks.active {
        right: 0;
    }
    .dropdownContent {
        position: relative;
        top: 0;
        right: 0;
        box-shadow: none;
        width: 100%;
    }
}
