.footer {
    width: 100%;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    box-shadow: 0 -3px 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    padding: 8px 0;
    position: fixed; /* Fixed at bottom */
    bottom: 0;
    left: 0;
    transition: all 0.3s ease;
}

/* Bottom navigation links */
.bottomNav {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.iconLink {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    text-decoration: none;
    color: #333;
    font-size: 12px;
    transition:
        transform 0.2s,
        color 0.2s;
}

.iconLink:hover {
    color: #00796b;
    transform: scale(1.1);
}

/* Desktop styles */
@media (min-width: 768px) {
    .footer {
        padding: 12px 0;
    }

    .bottomNav {
        gap: 40px; /* More spacing between links */
    }

    .iconLink {
        flex-direction: row; /* Icon + text side by side */
        gap: 8px;
        font-size: 14px;
    }
}
