.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 50px 20px 30px;
  background: #e0f7fa;
  border-radius: 15px;
  margin-bottom: 40px;
}

.hero h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #00796b;
}

.hero .siteName {
  color: #004d40;
}

.hero p {
  font-size: 1.1rem;
  color: #004d40;
}

/* Content Cards */
.content {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin-bottom: 40px;
}

.card {
  background: #ffffff;
  padding: 25px 20px;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.05);
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 25px rgba(0,0,0,0.08);
}

.card h2 {
  font-size: 1.6rem;
  color: #00796b;
  margin-bottom: 10px;
}

.card p {
  font-size: 1rem;
  line-height: 1.6;
  color: #004d40;
}

/* Footer Note */
.footerNote {
  text-align: center;
  background: #f1f8e9;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.05);
}

.footerNote p {
  font-size: 1rem;
  color: #004d40;
  font-weight: 500;
}

/* Responsive */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2rem;
  }

  .card h2 {
    font-size: 1.4rem;
  }

  .card p,
  .footerNote p {
    font-size: 0.95rem;
  }
}