.container {
    font-family: "Poppins", sans-serif;
    padding: 3rem 1rem;
    background: #f9faff;
    min-height: 100vh;
    text-align: center;
}

.header h1 {
    font-size: 2.8rem;
    color: #4a90e2;
    margin-bottom: 0.5rem;
    font-weight: 700;
}

.header h1 span {
    color: #00796b;
}

.header p {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 3rem;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.5;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 2rem;
    max-width: 1000px;
    margin: 0 auto;
}

.card {
    background: #fff;
    border-radius: 20px;
    padding: 2rem 1.5rem;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
    cursor: pointer;
    transition: transform 0.3s, box-shadow 0.3s;
    position: relative;
    overflow: hidden;
}

.card::before {
    content: "";
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(135deg, #d0e7ff, #74b9ff, #b2f2bb);
    opacity: 0;
    transform: rotate(25deg);
    transition: opacity 0.5s;
    z-index: 0;
}

.card:hover::before {
    opacity: 0.2;
}

.card:hover {
    transform: translateY(-6px);
    box-shadow: 0 16px 36px rgba(0, 0, 0, 0.12);
}

.card h2,
.card p {
    position: relative;
    z-index: 1;
}

.card h2 {
    font-size: 1.6rem;
    color: #4a90e2;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.card p {
    font-size: 1rem;
    color: #555;
    line-height: 1.5;
}

/* Extra scrolling content */
.extraContent {
    margin-top: 4rem;
    text-align: left;
    padding: 2rem 1rem;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.extraContent h2 {
    font-size: 2rem;
    color: #00796b;
    margin-bottom: 1rem;
}

.extraContent p {
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 2rem;
    line-height: 1.6;
}

.features {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
}

.featureCard {
    background: #fff;
    border-radius: 15px;
    padding: 1.5rem;
    flex: 1 1 250px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s, box-shadow 0.3s;
}

.featureCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 28px rgba(0, 0, 0, 0.08);
}

.featureCard h3 {
    color: #4a90e2;
    margin-bottom: 0.5rem;
}

.featureCard p {
    color: #555;
    font-size: 1rem;
}

/* Responsive */
@media screen and (max-width: 768px) {
    .header h1 {
        font-size: 2rem;
    }

    .header p {
        font-size: 1rem;
    }

    .card h2 {
        font-size: 1.4rem;
    }

    .card p {
        font-size: 0.95rem;
    }

    .extraContent h2 {
        font-size: 1.6rem;
    }

    .extraContent p {
        font-size: 0.95rem;
    }

    .featureCard h3 {
        font-size: 1.2rem;
    }

    .featureCard p {
        font-size: 0.9rem;
    }
}